import $ from 'jquery';
import ISubject from '../ISubject';
import AppSettingsBarView from '../Views/appSettingsBarView/AppSettingsBarView';
import SettingsEvent, { SETTINGS_EVENT_ENUM } from '../Models/AppSettingsEvent';

const MIN_VALUE = 32;
const MED_VALUE = 64;
const MAX_VALUE = 128;

export default class AppSettingsBarController extends ISubject {
  constructor(keyboardService) {
    super();
    this.element = new AppSettingsBarView();
    this.keyboardService = keyboardService;
  }

  init(container) {
    this.element.render(container);
  }

  setFrameInfo(eventData) {
    const { currentFrame, numberOfFrames } = eventData;
    $(`#${this.element.frameInfoId}`).html(`${currentFrame + 1} / ${numberOfFrames}`);
  }

  notifyChange(event) {
    if (event.type === SETTINGS_EVENT_ENUM.MODEL_STRUCTURE_CHANGE) {
      this.setFrameInfo(event.data);
    }
  }

  setCanvasInfo(value) {
    $(`#${this.element.canvasInfoId}`).html(`[ ${value} x ${value} ]`);
  }

  changeCanvasSize(value) {
    const event = new SettingsEvent(SETTINGS_EVENT_ENUM.RESIZE_BUTTON_CLICK, value);
    this.notify(event);
    $(`#${this.element.canvasInfoId}`).html(`[ ${value} x ${value} ]`);
  }

  updateCoords(coords) {
    const { indexX, indexY } = coords;
    $(`#${this.element.coordsInfoId}`).html(`${indexX} : ${indexY}`);
  }

  clearCoordinates() {
    $(`#${this.element.coordsInfoId}`).html('');
  }

  listen() {
    $(`#${this.element.minButtonId}`).on({
      change: () => this.changeCanvasSize(MIN_VALUE),
    });
    $(`#${this.element.medButtonId}`).on({
      change: () => this.changeCanvasSize(MED_VALUE),
    });
    $(`#${this.element.largeButtonId}`).on({
      change: () => this.changeCanvasSize(MAX_VALUE),
    });
  }
}
