import $ from 'jquery';
import './headerStyles.less';

export default class AppHeaderView {
  constructor() {
    this.template = `<header class="header">
      <a class="header__app-title-link" id="header__app-title" href="#"><h1 class="header__title">Lobster</h1></a>
      <h2 class="header__title">New Lobster</h2>
      <button class="header__button" id="header__button--create">Create Sprite</button>
      <button class="header__button" id="header__button--sign_in">Sign In</button>
    </header>`;
  }

  render(container) {
    $(`#${container}`).append(this.template);
  }
}
